.custom-selection span input[type="radio"]{position: absolute;z-index: -1;opacity: 0;top:7px;left:2px;}
.custom-selection span input[type="radio"]:checked  ~ label::before{color: #fff;border-color: #4ab6ec;background-color: #22b4c1;}
.custom-selection span input[type="radio"]:focus  ~ label::before{box-shadow: 0 0 0 0.2rem rgba(77, 182, 189, 0.25);}
.custom-selection span input[type="radio"]:focus:not(:checked) ~ label::before{border-color: #50c7cf;}
.custom-selection span input[type="radio"]:not(:disabled):active ~ label::before{color: #fff;background-color: #ffffff;border-color: #28a3ae;} 
.custom-selection span input[type="radio"]:disabled ~ label{color: #6c757d;}
.custom-selection span input[type="radio"]:disabled ~ label::before{background-color: #e9ecef;}
.custom-selection span label {position: relative;margin-bottom: 0;vertical-align: top;}
.custom-selection span label::before {position: absolute;top: 0.25rem;left:-1.3rem;display: block;width: 1rem;height: 1rem;pointer-events: none;content: "";background-color: #fff;border: #28a3ae solid 1px;}
.custom-selection span label::after {position: absolute;top: 0.25rem;left:-1.3rem;display: block;width: 1rem;height: 1rem;content: "";background-repeat: no-repeat;background-position: center center;background-size: 50% 50%;}
.custom-selection span label::before {border-radius: 0;}
.custom-selection span input[type="radio"] ~ label::before {border-radius: 50%;}
.custom-selection span input[type="radio"]:checked ~ label::after {background-image: url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='-4 -4 9 9'%3e%3ccircle r='4' fill='%2328a3ae'/%3e%3c/svg%3e");}
.custom-selection span input[type="radio"]:checked ~ label::before {background-color:#ffffff;}
.custom-selection span input[type="radio"]:disabled:checked ~ label::before{background-color:#c5c5c5;border: 1px solid #bbbbbb;}
