/* The container */
.log-cont {
position: relative;
padding-left: 25px;
margin-bottom: 12px;
cursor: pointer;
-webkit-user-select: none;
-moz-user-select: none;
-ms-user-select: none;
user-select: none;
}

/* Hide the browser's default checkbox */
.log-cont input {
position: absolute;
opacity: 0;
cursor: pointer;
height: 0;
width: 0;
}
.custom-control-label::before{width:20px;height:20px;top:0;left:-1.8rem;}
.custom-control-label::after{width:20px;height:20px;top:0;left:-1.8rem;}
.custom-control-input:checked ~ .custom-control-label::before{color: #fff;border-color: #50B6BD;background-color: #fff;}
.custom-radio .custom-control-input:checked ~ .custom-control-label::after {
  background-image: url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='-5 -5 10 10'%3e%3ccircle r='5' fill='%2350B6BD'/%3e%3c/svg%3e");transform: scale(1.2, 1.2);}
.custom-control-input[type="checkbox"]:checked ~ .custom-control-label::before{color: #fff;border-color: #50B6BD;background-color: #50B6BD;}
