.back-color{background-color:#efefef;}
.scan-btn{background:url(../../src/img/Scan-icon-18px.png) no-repeat center center #28a3ae;border:none;border-radius:5px;padding:20px;}
.form-control-1{background: #fff;border: none;border: 1px solid #28A3AE;height: 38px;width: 85%;padding: 10px;border-radius: 5px;flex-basis:85%;margin-right:10px;}
.list-grid, .list-data{margin:0;padding:0;width:100%;border-collapse: collapse;table-layout:fixed;}
.list-grid thead tr {border-bottom:1px solid #e7e7e7;}
.list-grid thead tr th:nth-child(1){width:40%;text-align:left;padding:5px 2px;color:#a9a9a9;}
.list-grid thead tr th:nth-child(2){width:75%;text-align:center;padding:5px 2px;color:#a9a9a9;}
.list-grid thead tr th:nth-child(3){width:20%;text-align:center;padding:5px 2px;color:#a9a9a9;}
.list-grid thead tr th:nth-child(4){width:20%;text-align:center;padding:5px 2px;color:#a9a9a9;}
.list-data  tr{border-bottom:1px solid #aaaaaa;width:100%;display:table;}
.list-data  tr td:nth-child(1){width:40%;text-align:left;padding:5px 2px;vertical-align:middle;}
.list-data  tr td:nth-child(2){width:75%;text-align:center;padding:5px 2px;vertical-align:middle;}
.list-data  tr td:nth-child(3){width:20%;text-align:center;padding:5px 2px;vertical-align:middle;}
.list-data  tr td:nth-child(4){width:20%;text-align:center;padding:5px 2px;vertical-align:middle;}
.footer{position:fixed;bottom:0;width:100%;color:#000000;padding:5px 0;}
.f-home, .f-suspend, .f-resume, .f-more, .f-checkout{display:block;padding:65px 0 5px 5px;min-width:60px;margin:0 5px;text-align:center;}
.f-home{background:url(../../src/img/home_new.png) scroll no-repeat top center transparent;}
.f-suspend{background:url(../../src/img/suspend.png) scroll no-repeat top center transparent;}
.f-resume{background:url(../../src/img/resumetxn.png) scroll no-repeat top center transparent;}
.f-more{background:url(../../src/img/more_tender.png) scroll no-repeat top center transparent;}
.f-checkout{background:url(../../src/img/icon_checkout.png) scroll no-repeat top center transparent;}
.foot-top{width:100%;padding:5px;background-color:#6cd3f6;color:#000000;display:flex;flex-wrap:wrap;flex-direction: row;justify-content: space-between;}
.foot-middle{width:100%;padding:5px;background-color:#65a3de;color:#000000;}
.back-white{background-color:#ffffff;}
.dc-input{display: flex;flex-wrap:wrap;flex-direction: row;justify-content: center;align-items: center;flex-basis:85%;position:relative;margin-right:10px;}
