.pad-5{padding:5px;}
.pay-type{display:flex;align-items: center;border-bottom:1px solid #aaaaaa;color:#000000;}
.pay-type:visited{text-decoration:none;color:#000000;}
.pay-type:hover{text-decoration:none;color:#000000;}
.pay-type:focus{text-decoration:none;color:#000000;}
.pay-type img {margin:10px 25px 10px 10px;width:32px;}
.pay-scroll{height:83%;overflow-y: scroll;}
.modal-header{border-bottom:none;}
.popup-title{text-align:center;font-size:16px;font-family: 'JioType';font-weight: 500;font-style: normal;}
.form-horizontal .input-label {padding-top: 7px;margin-bottom: 0;text-align: left;font-weight: 300;font-style: normal;font-size:16px;}
.form-input-control{border:none;border-bottom:1px solid #A2A2A2;width:100%;padding:8px 10px;}
.button{
	display: inline-block;
	padding: 6px 20px;
	margin-bottom: 0;
	font-size: 16px;
	font-weight: 400;
	line-height: 1.42857143;
	text-align: center;
	white-space: nowrap;
	vertical-align: middle;
	-ms-touch-action: manipulation;
	touch-action: manipulation;
	cursor: pointer;
	-webkit-user-select: none;
	-moz-user-select: none;
	-ms-user-select: none;
	user-select: none;
	background-image: none;
	border-radius: 4px;
	}
.btn-cancel-md{border:1px solid #000000;color:#000000;background-color:#ffffff;font-weight: 300;font-style: normal;width:49%;}
.btn-pay-md{border: 1px solid #03007f;color:#ffffff;background-color:#03007f;font-weight: 500;font-style: normal;width:49%;}
.pt-20{padding-top:20px;}

.modalcash {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1050;
  display: none;
  width:100%;
  height:100%;
  overflow: hidden;
  outline: 0;
  }
.modal-backdrop {
    position: fixed;
    top: 0;
    left: 0;
    z-index: 1040;
    width: 100%;
    height: 100%;
    background-color: #000;
}

  .react-confirm-alert-body {
    font-family: "JioTypeW04-Light";
    width: 300px;
    padding: 25px;
    text-align: left;
    background: #fff;
    border-radius: 10px;
    box-shadow: 0 20px 75px rgba(0, 0, 0, 0.13);
    color: #666;
}

  .react-confirm-alert-button-group > button {
    outline: none;
    background:#024C9D;
    border: none;
    display: inline-block;
    padding: 6px 18px;
    color: #eee;
    margin-right: 10px;
    border-radius: 5px;
    font-size: 12px;
    cursor: pointer;
  }
  .react-confirm-alert-overlay {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 99;
    background: rgba(255, 255, 255, 0.9);
    display: -webkit-flex;
    display: -moz-flex;
    display: -ms-flex;
    display: -o-flex;
    display: flex;
    justify-content: center;
    -ms-align-items: center;
    align-items: center;
    opacity: 0;
    -webkit-animation: react-confirm-alert-fadeIn 0.5s 0.2s forwards;
    -moz-animation: react-confirm-alert-fadeIn 0.5s 0.2s forwards;
    -o-animation: react-confirm-alert-fadeIn 0.5s 0.2s forwards;
    animation: react-confirm-alert-fadeIn 0.5s 0.2s forwards;
    z-index: 5000;
  }
 
  
